.header {
    height: 64px;
    background-color: #fff;
    border-bottom: 1px solid #ececec;

    &__logo {
        font-family: 'Righteous', cursive;
        font-size: 32px;
        color: #c53431;
        text-decoration: none;
        line-height: 60px;
        margin-left: 32px;

        &:hover {
            color: #c53431;
            text-decoration: none; } }

    &__links {
        float: right;
        margin-right: 24px;

        &__link {
            text-decoration: none;
            color: #484848;
            line-height: 64px;
            font-weight: 500;
            margin: 0 12px;

            &-active {
                color: #242424;
                font-weight: 900; } } } }

